import React from 'react'
import {Link} from 'react-router-dom'
import AnchorLink from 'react-anchor-link-smooth-scroll'
import './style.css'


const Welcome = () =>{
    return(
        <div className="welcome-area">
            <div className="container">
                <div className="row">
                    <div className="col-lg-12">
                        <div className="welcome-content">
                            <h2>초대합니다</h2>
                            <p>
                                서로 다른 곳에서 살아온 두 별이 
                                <br/>소중한 별을 맞이하여
                                <br/>새로운 시작을 하게 되었습니다.
                                <br/>별들의 시작을 알리고,
                                <br/>사랑하는 사람들과 함께 나누고자
                                <br/>이 초대장을 보냅니다.
                            </p>
                            <div className="btn"><AnchorLink href='#event'>오시는길</AnchorLink></div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    )
}

export default Welcome;