import React, { useState } from 'react';
import AudioPlayer, {RHAP_UI} from 'react-h5-audio-player';
import 'react-h5-audio-player/lib/styles.css';
import './style.css'
import Music from './MyValentine_StandardQuality.mp3'
import Music2 from './Stelle.mp3'
// import 'react-h5-audio-player/lib/styles.less' Use LESS
// import 'react-h5-audio-player/src/styles.scss' Use SASS


const Player = () => {
  let [musicIndex,setmusicIndex] = useState(Math.floor(Math.random() * 2));
  let playList = [
    {name : 'My Valentine♬ - Oak Studios', src: Music},
    {name : 'Stelle♬ - Oak Studios', src: Music2},
  ]
  let [nowplay,setNowPlay] = useState(playList[musicIndex]);
  

  return(
    <AudioPlayer
      autoPlay={true}
      src={playList[musicIndex].src}
      // other props here
      // loop={true}
      layout="horizontal-reverse"
      customProgressBarSection={
        [
          RHAP_UI.CURRENT_TIME,
          <div style={{width:'10px'}}></div>,
          <div className='animated-text'>
            <span>{playList[musicIndex].name}</span>
          </div>
        ]
      }
      customAdditionalControls={[]}
      customVolumeControls={
        [
        ]
      }
      customControlsSection={
        [
          RHAP_UI.MAIN_CONTROLS
        ]
      }
      showJumpControls={false}
      onPlay={()=>{
        let textDiv = document.querySelector('.animated-text span');
        textDiv.style.animationPlayState = "running";
      }}
      onPause={()=>{
        let textDiv = document.querySelector('.animated-text span');
        textDiv.style.animationPlayState = "paused";
      }}
      onEnded={()=>{
        musicIndex === 0 ? setmusicIndex(1) : setmusicIndex(0);
      }}
    />
  );
}

export default Player;
