import React from 'react'
import {Link} from 'react-router-dom'
import Sectiontitle from '../section-title'
import couple1 from '../../images/couple/img-1.jpg'
import couple2 from '../../images/couple/img-2.jpg'
import couple3 from '../../images/couple/2.png'
import './style.css'

const Couple = () => {
    return(
        <div id="couple" className="about-wrap">
            <div className="couple-area section-padding pb-70">
                <Sectiontitle section={'신랑&신부'}/>
                <div className="container">
                    <div className="couple-wrap">
                        <div className="row">
                            <div className="col-lg-5 col-md-12 col-sm-12 couple-single">
                                <div className="couple-wrap couple-wrap-2">
                                    <div className="couple-img">
                                        <img src={couple1} alt="thumb"/>
                                    </div>
                                    <div className="couple-text">
                                        <div className="couple-content">
                                            <h3 className="parent-title">(부)김유석 · (모)김완순의 차남</h3>
                                            <h3>김태욱</h3>
                                            <p>안녕하세요! 마냥 철없던 욱이가 결혼을 하는 날이 왔습니다.😭
                                                <br/>축복스러운 자리에 초대하게 되어서 너무 기쁘고 
                                                <br/>많은 축하 부탁드립니다.🙏
                                                <br/>저희의 결혼식이 무더운 8월의 여름방학과도 같은 
                                                <br/>꿀같은 날이 되기를 바랍니다.
                                                <br/>여러분들께 받은 수많은 축복들
                                                <br/>베풀며 살 수 있는 부부가 되도록 하겠습니다. 
                                                <br/>그리고 서로가 행복해질 수 있도록 노력하겠습니다.👩🏻‍❤️‍👨🏻
                                            </p>
                                        </div>
                                        <div className="social-icon">
                                            <ul className="d-flex">
                                                {/* <li><Link to='/'><span className="fa fa-facebook"></span></Link></li>
                                                <li><Link to='/'><span className="fa fa-twitter"></span></Link></li>
                                                <li><Link to='/'><span className="fa fa-google-plus"></span></Link></li>
                                                <li><Link to='/'><span className="fa fa-linkedin"></span></Link></li> */}
                                            </ul>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-lg-2 couple-single">
                                <div className="couple-shape">
                                   <img src={couple3} alt="shape"/>
                                </div>
                            </div>
                            <div className="col-lg-5 col-md-12 col-sm-12 couple-single">
                                <div className="couple-wrap couple-wrap-3">
                                    <div className="couple-img couple-img-2">
                                        <img src={couple2} alt="thumb"/>
                                    </div>
                                    <div className="couple-text">
                                        <div className="couple-content">
                                            <h3 className="parent-title">(부)강학수 · (모)김순희의 장녀</h3>
                                            <h3>강수지</h3>
                                            <p>
                                                안녕하세요! 신부 강수지입니다!
                                                <br/>어떤 말로 서두를 떼어야 할 지 모르겠지만..
                                                <br/>네! 저도 결혼이라는 걸 하게 되었습니다! 😊
                                                <br/>그것도 제가 너무나 사랑하고 아끼며 소중하게 여기던 사람과요!
                                                <br/>그래서인지 짧은 기간 동안 준비 하게 된 결혼임에도 불구하고 
                                                <br/>요즘의 저는 너무나도 행복한 일상을 보내고 있습니다 ㅎㅎ
                                                <br/>한창 더위가 기승을 부릴 8월달의 한 여름날,
                                                <br/>결혼을 하게 된 저희를 위해서 먼 길 소중한 시간 내 오셔서
                                                <br/>축복해주신 모든 분들께 진심으로 감사의 말씀 전해드리며
                                                <br/>꼭 저희 두 사람 서로 지금처럼 아껴주고 
                                                <br/>사랑하는 변함 없는 모습으로
                                                <br/>여러분들께 받은 축복의 힘을 보여드릴 수 있도록 
                                                <br/>열심히 잘 살아보겠습니다!!
                                                <br/>이 곳에 들려주신 모든 분들께 다시 한번 감사드립니다! 
                                                <br/>오늘 하루도 사랑하는 분들과 행복한 하루 보내시길 바랍니다~❤
                                            </p>
                                        </div>
                                        <div className="social-icon">
                                            <ul className="d-flex">
                                                {/* <li><Link to='/'><span className="fa fa-facebook"></span></Link></li>
                                                <li><Link to='/'><span className="fa fa-twitter"></span></Link></li>
                                                <li><Link to='/'><span className="fa fa-google-plus"></span></Link></li>
                                                <li><Link to='/'><span className="fa fa-linkedin"></span></Link></li> */}
                                            </ul>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
             
        </div>
        
    )
}

export default Couple;